.recipeCardLoader__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-radius: .4rem;
    background: var(--color-background);
    width: 20rem;
    height: 20rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
    cursor: pointer;
}

.recipeCardLoader__header {
    font-family: var(--font-family-primary);
    text-align: center;
    font-size: 2rem;
    padding: 1rem 0;
    margin-bottom: 2rem;
}
