@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rokkitt:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  --color-text: black;
  --color-background: white;
  --color-background-hover: #F1F1F1;
  --color-primary: #E7ECE6;
  --color-secondary: #F3F6F2;
  --font-family-primary: 'Bebas Neue', cursive;
  --font-family-secondary: 'Rokkitt', serif;
  --header-font-size: 7rem;
  --header-y-padding: 5rem;
  --subheader-font-size: 2.5rem;
  --side-page-padding: 7rem;
  --back-arrow-width: 2rem;
  font-size: 62.5%;
}

@media only screen and (max-width: 600px) {
  html {
    --side-page-padding: 4rem;
    --header-font-size: 5rem;
    --header-y-padding: 3rem;
    --subheader-font-size: 2rem;
    --back-arrow-width: 1rem;
  }
}

.App {
  font-size: 1.6rem;
}

.backArrow {
  cursor: pointer;
  width: 2rem;
  width: var(--back-arrow-width);
}

.app__cardsWrapper {
  display: flex;
  justify-content: center;
  background: #E7ECE6;
  background: var(--color-primary);
  padding: 2rem 7rem;
  padding: 2rem var(--side-page-padding);
  margin-bottom: 3rem;
}

.app__cardsContainer {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  grid-gap: 6rem;
  margin: 4rem 0;
  width: 100%;
}

.app__infoSpan {
  font-family: 'Rokkitt', serif;
  font-family: var(--font-family-secondary);
  font-size: 2rem;
}

.recipeDetails__headerWrapper {
  background: #E7ECE6;
  background: var(--color-primary);
  padding: 4rem 7rem;
  padding: 4rem var(--side-page-padding);
  margin: 5% 0;
  display: flex;
  align-items: center;
}

.recipeDetails__headerText {
  font-family: 'Bebas Neue', cursive;
  font-family: var(--font-family-primary);
  font-size: 7rem;
  font-size: var(--header-font-size);
  margin-left: 4rem;
}

.recipeDetails__contentWrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 2rem 7rem;
  padding: 2rem var(--side-page-padding);
}

.recipeDetails__recipeContent {
  display: flex;
  flex-direction: column;
  margin-right: 4rem;
  width: 40rem;
}

.recipeDetails__recipeContentHeader {
  font-family: 'Bebas Neue', cursive;
  font-family: var(--font-family-primary);
  font-size: 2.5rem;
}

.recipeDetails__contentSeparator {
  height: 2rem;
  width: 100%;
  border: none;
  background: #E7ECE6;
  background: var(--color-primary);
}

.recipeDetails__ingredientsList {
  font-family: 'Rokkitt', serif;
  font-family: var(--font-family-secondary);
  font-size: 2rem;
  font-weight: 700;
  line-height: 4rem;
}

.recipeDetails__instructionsText {
  font-family: 'Rokkitt', serif;
  font-family: var(--font-family-secondary);
  font-size: 2rem;
  font-weight: 700;
  line-height: 4rem;
}

.recipeDetails__mealImage {
  width: 30rem;
  height: 30rem;
  border-radius: .6rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
}
.categoryCard__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-radius: .4rem;
    padding: 2rem;
    background: var(--color-background);
    width: 20rem;
    height: 20rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
    cursor: pointer;
}

.categoryCard__wrapper:hover {
    background: var(--color-background-hover);
}

.categoryCard__header {
    font-family: var(--font-family-primary);
    text-align: center;
    font-size: 2.5rem;
}

.categoryCard__imgWrapper {
    display: flex;
    justify-content: center;
}

.categoryCard__img {
    width: 14rem;
}
.page__headerWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.page__headerText {
    font-family: var(--font-family-primary);
    font-size: var(--header-font-size);
    padding: 0 var(--side-page-padding);
    margin: var(--header-y-padding) 0;
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  .page__headerLogo {
    width: 5rem;
    margin: 0 .8rem;
  }
  
  .page__subHeader {
    font-family: var(--font-family-secondary);
    font-size: var(--subheader-font-size);
    padding: 3rem var(--side-page-padding);
    font-weight: 700;
    width: 100%;
    text-align: center;
    background: var(--color-secondary);
  }

.page__filtersWrapper {
  font-family: var(--font-family-secondary);
  font-size: var(--subheader-font-size);
  padding: 3rem var(--side-page-padding);
  font-weight: 700;
  width: 100%;
  display: flex;
  justify-content: center;
  background: var(--color-secondary);
}

.page__filterOption {
  margin: 0 2em;
  cursor: pointer;
}

.page__filterActive {
  text-decoration: underline;
}
.categoryCardLoader__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-radius: .4rem;
    padding: 2rem;
    background: var(--color-background);
    width: 20rem;
    height: 20rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
    cursor: pointer;
}

.categoryCardLoader__header {
    font-family: var(--font-family-primary);
    text-align: center;
    font-size: 2rem;
}

.categoryCardLoader__imgWrapper {
    display: flex;
    justify-content: center;
}
.textCard__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-radius: .4rem;
    padding: 2rem;
    background: var(--color-background);
    width: 20rem;
    height: 5rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
    cursor: pointer;
}

.textCard__wrapper:hover {
    background: var(--color-background-hover);
}

.textCard__header {
    font-family: var(--font-family-primary);
    text-align: center;
    font-size: 2.5rem;
}

.textCard__imgWrapper {
    display: flex;
    justify-content: center;
}

.textCard__img {
    width: 14rem;
}

.recipeCard__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    border-radius: .4rem;
    background: var(--color-background);
    width: 20rem;
    height: 20rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
    cursor: pointer;
    position: relative;
}

.recipeCard__wrapper:hover {
    background: var(--color-background-hover);
}

.recipeCard__headerWrapper {
    background: white;
    width: 100%;
    padding: 1rem 0;
    margin-bottom: 2rem;
    z-index: 1;
}

.recipeCard__header {
    font-family: var(--font-family-primary);
    text-align: center;
}

.recipeCard__img {
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: inherit;
}
.recipeCardLoader__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-radius: .4rem;
    background: var(--color-background);
    width: 20rem;
    height: 20rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
    cursor: pointer;
}

.recipeCardLoader__header {
    font-family: var(--font-family-primary);
    text-align: center;
    font-size: 2rem;
    padding: 1rem 0;
    margin-bottom: 2rem;
}

.recipeDetailsLoader__ingredientsList {
    margin: 1rem 0;
}

.recipeDetailsLoader__ingredients {
    margin: .5rem 0;
}

.recipeDetailsLoader__instructions {
    height: 6rem;
    margin: 1.5rem 0;
}

.recipeDetailsLoader__mealImageWrapper {
    display: flex;
    flex-direction: column;
}

.recipeDetailsLoader__mealImage {
    height: 30rem;
    width: 30rem;
}
